$stepTiming: 0.8s 0.2s;
.training-service-page {
  .banner {
    width: 100%;
    height: 100px;
    background: url(../../../assets/img/ic-banner-training.png);
    background-repeat: no-repeat;
    background-size: cover;
    .title-banner {
      font-size: 36px;
      text-align: center;
      color: #ffffff;
      font-family: Arial;
      line-height: 100px;
      width: 100%;
    }
  }
  .course-layout {
    width: 100%;
    background-color: #ececec;
    .course-box {
      max-width: 1170px;
      margin: auto;
      padding: 45px 0 62px 0;
      background-color: #ececec;

      .course-content {
        display: flex;
        .list-course-box {
          flex-grow: 1;
          padding-right: 30px;
          .select-course-type {
            display: flex;
            border-bottom: 1px solid #f8fcfe;
            .type-course {
              width: 192px;
              height: 73px;
              background-color: #ffffff;
              color: #333333;
              font-size: 16px;
              font-family: Arial;
              font-weight: bold;
              text-align: center;
              line-height: 73px;
              cursor: pointer;
              &.active {
                position: relative;
                background-color: #125491;
                color: #ffffff;
                &::after {
                  content: "";
                  position: absolute;
                  left: 42%;
                  top: 100%;
                  width: 0;
                  height: 0;
                  border-left: 12px solid transparent;
                  border-right: 12px solid transparent;
                  border-top: 11px solid #125491;
                  clear: both;
                  transition: $stepTiming;
                }
              }
            }
          }
          .list-course {
            display: flex;
            flex-wrap: wrap;
            // width: 770px;
            .course-item {
              // width: 370px;
              // height: 216px;
              margin-top: 20px;
              background-color: #ffffff;
              padding: 10px;
              font-size: 13px;
              color: #555;
              &:nth-child(odd) {
                margin-right: 20px;
              }

              .time {
                background-color: #125491;
                height: 40px;
                width: 110px;
                font-size: 12px;
                font-family: "OpenSans";
                color: #fefefe;
                text-align: center;
                line-height: 18px;
                float: left;
                margin-right: 10px;

                span {
                  font-family: "OpenSans-Bold";
                  font-size: 20px;
                  font-weight: 600;
                }
              }
              .title {
                font-family: Arial;
                color: #333333;
                line-height: 20px;
                font-size: 16px;
                font-weight: bold;
                text-align: justify;
                margin-bottom: 5px;
              }
              .box-left {
                float: left;
                width: 100%;
                .course-title {
                  span {
                    font-weight: bold;
                    color: #005197;
                  }
                }
              }
              .box-right {
                float: left;
                width: 100%;
                text-align: left;
                .course-title {
                  span {
                    font-weight: bold;
                    color: #005197;
                  }
                }
              }
              .course-des {
                display: inline-block;
                text-align: justify;
                line-height: 18px;
                padding: 5px 0;
                white-space: nowrap;
                width: 360px;
                overflow: hidden;
                height: 30px;
                text-overflow: ellipsis;
                &.show-more {
                  white-space: pre-wrap;
                  height: unset;
                }
              }
              .course-footer {
                display: inline-flex;
                .file-download {
                  display: inline-flex;
                  color: #555;
                  margin-right: 15px;
                  border-right: 1px solid #ccc;
                  padding-right: 15px;
                  .icPdf {
                    content: url("../../../assets/img/icPdf.png");
                    width: 17px;
                    height: 19px;
                    margin-left: 3px;
                  }
                }
                .regLean {
                  font-weight: bold;
                  color: #005197;
                  cursor: pointer;
                }
              }
              .icAdMore {
                content: url("../../../assets/img/icAdMore.png");
                width: 13px;
                height: 14px;
                margin-left: 3px;
                cursor: pointer;
              }
              .icSubMore {
                content: url("../../../assets/img/icSubMore.png");
                width: 13px;
                height: 14px;
                margin-left: 3px;
                cursor: pointer;
              }
            }
          }
        }
        .hot-course {
          flex-basis: 31%;
          height: 563px;
          background-color: #ffffff;
          .hot-title {
            background-color: #d71921;
            color: #ffffff;
            padding-left: 34px;
            height: 54px;
            line-height: 54px;
            font-size: 16px;
            font-weight: bold;
            text-transform: uppercase;
          }
          .course-item {
            padding: 10px;
            position: relative;
            border-bottom: 1px solid #eee;
            .course-name {
              display: inline-block;
              color: #333333;
              font-size: 14px;
              line-height: 20px;
              font-family: Arial;
              padding-right: 20px;
              margin-bottom: 5px;
              .course-index {
                font-size: 24px;
                color: #333333;
                margin-right: 10px;
                font-weight: 900;
                width: 20px;
                position: relative;
                float: left;
              }
              span {
                display: inline;
              }

              &:hover {
                text-decoration: underline;
                cursor: pointer;
                color: #005197;
              }
            }

            &.active {
              .course-name {
                color: #005197;
                .course-index {
                  color: #005197;
                }
              }
              &::after {
                content: url("../../../assets/img/icArrowUp.png");
                width: 15px;
                height: 10px;
                position: absolute;
                right: 15px;
                top: 5px;
              }

              .time {
                background-color: #125491;
                height: 40px;
                width: 87px;
                font-size: 12px;
                font-family: "OpenSans";
                color: #fefefe;
                text-align: center;
                line-height: 19px;
                float: left;
                margin-right: 10px;

                span {
                  font-family: "OpenSans-Bold";
                  font-size: 23px;
                  font-weight: 600;
                }
              }
              .title {
                font-family: Arial;
                color: #333333;
                line-height: 20px;
                font-size: 16px;
                font-weight: bold;
                text-align: justify;
                padding-bottom: 5px;
              }
              .box-left {
                float: left;
                font-size: 13px;
                color: #555;
                width: 100%;
                .course-title {
                  span {
                    font-weight: bold;
                    color: #005197;
                  }
                }
              }
              .box-right {
                float: left;
                text-align: left;
                font-size: 13px;
                color: #555;
                width: 100%;
                .course-title {
                  span {
                    font-weight: bold;
                    color: #005197;
                  }
                }
              }
              .course-des {
                display: inline-block;
                text-align: justify;
                line-height: 18px;
                padding: 8px 0;
                color: #555;
                font-size: 13px;
              }
              .course-footer {
                display: inline-flex;
                font-size: 13px;
                color: #555;
                .file-download {
                  display: inline-flex;
                  color: #555;
                  margin-right: 15px;
                  border-right: 1px solid #ccc;
                  padding-right: 15px;
                  .icPdf {
                    content: url("../../../assets/img/icPdf.png");
                    width: 17px;
                    height: 19px;
                    margin-left: 3px;
                  }
                }
                .regLean {
                  font-weight: bold;
                  color: #005197;
                  cursor: pointer;
                }
              }
            }
            &::after {
              content: url("../../../assets/img/icArrowDownB.png");
              width: 15px;
              height: 10px;
              position: absolute;
              right: 15px;
              top: 5px;
            }
          }
          .row-course {
            padding: 0 10px;
          }
        }
      }
    }
  }
  .type-course-layout {
    padding: 30px 0 20px 0;
    max-width: 1170px;
    margin: auto;
    .title {
      font-size: 26px;
      font-family: Arial;
      font-weight: bold;
      line-height: 22px;
      color: #333333;
      text-align: center;
    }
    hr {
      height: 2px;
      width: 41px;
      margin: 21px auto 34px auto;
      background-color: #29323c;
    }
    .select-type-course {
      display: flex;
      .item-type-course {
        flex-grow: 1;
        text-align: center;
        border-top: solid 1px #ddd;
        border-right: solid 1px #ddd;
        color: #333333;
        font-size: 18px;
        height: 50px;
        line-height: 50px;
        cursor: pointer;
        &.selected {
          background-color: #005197;
          color: #ffffff;
        }
        &:first-child {
          border-left: solid 1px #ddd;
        }
      }
    }
  }
  .teachers-layout {
    min-height: 700px;
    padding-top: 63px;
    background: url("../../../assets/img/expert-bg.png");
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    .teachers-title {
      font-size: 26px;
      line-height: 22px;
      text-align: center;
      color: #ffffff;
      font-weight: bold;
    }
    hr {
      background-color: #fff;
      height: 2px;
      width: 40px;
      margin: 21px auto 23px auto;
    }
    .teacher-des {
      font-size: 16px;
      color: #ffffff;
      line-height: 22px;
      text-align: center;
      max-width: 635px;
      margin: auto;
    }
    .teacher-special {
      font-size: 26px;
      color: #ffffff;
      line-height: 22px;
      text-align: center;
      margin-top: 32px;
    }
    .slick-slider {
      max-width: 1170px;
      margin: auto;
      font-family: Arial;
      padding: 0 40px;
      display: flex;
      img {
        height: 210px;
        border-radius: 50%;
        margin: auto;
      }
      .teacher-name {
        font-size: 18px;
        color: #ffffff;
        margin-top: 27px;
        text-align: center;
      }
      .teacher-title {
        font-size: 18px;
        color: rgba(250, 250, 250, 0.7);
        margin-bottom: 21px;
        text-align: center;
      }
      .teacher-detail {
        font-size: 16px;
        line-height: 24px;
        text-align: center;
        color: #fafafa;
      }
      .slick-list .slick-track .slick-slide {
        padding: 30px;
      }
      .slick-next {
        background: url(../../../assets/img/next-arrowG.png);
        width: 30px;
        height: 50px;
        right: 0px;
        &::before {
          content: "";
        }
        &:hover {
          background: url(../../../assets/img/next-arrowHv.png);
        }
      }
      .slick-prev {
        background: url(../../../assets/img/back-arrowG.png);
        width: 30px;
        height: 50px;
        left: 0px;
        &::before {
          content: "";
        }
        &:hover {
          background: url(../../../assets/img/back-arrowHv.png);
        }
      }
    }
    .slick-dots {
      display: none !important;
    }
  }
  .type-course-layout {
    .type-course-list {
      background-color: #f9f9f9;
      border: 1px solid #e1e1e1;
      padding: 20px;
      .item-course {
        background-color: #ffffff;
        border: 1px solid #eee;
        padding: 15px;
        position: relative;
        color: #555555;
        font-size: 14px;
        line-height: 22px;
        margin-bottom: 20px;
        &:last-child {
          margin-bottom: 0px;
        }
        .course-index {
          position: absolute;
          background-color: #cc0000;
          font-size: 22px;
          font-weight: 900;
          line-height: 38px;
          width: 40px;
          height: 40px;
          color: #fff;
          text-align: center;
          vertical-align: middle;
          left: 0px;
          top: -6px;
        }
        span {
          font-weight: bold;
          color: #005197;
        }
        .sqrL {
          position: absolute;
          height: 0px;
          width: 0px;
          border-left: 6px solid #999;
          border-right: 6px solid transparent;
          border-top: 6px solid transparent;
          left: 40px;
          top: -6px;
        }
        .course-name {
          font-size: 18px;
          line-height: 30px;
          padding-left: 40px;
          color: #222;
        }
        .course-date-time-cost {
          line-height: 24px;
          display: inline-block;
          padding-right: 15px;
        }
        .course-des {
          margin: 7px 0px;
          line-height: 22px;
          text-align: justify;
        }
        .blInfo {
          display: flex;
          flex-basis: 100%;
        }
        table {
          // display: inline-block;
          flex-basis: 56%;
          border-collapse: separate;
          border-spacing: 0px;
          thead {
            th {
              display: inline-block;
              padding: 0px 20px;
              text-align: center;
              margin: -2px;
              &:nth-child(1) {
                width: 115px;
                border-right: 1px solid #ddd;
                padding: 0px 20px 0px 0px;
              }
              &:nth-child(2) {
                width: 165px;
                border-right: 1px solid #ddd;
              }
              &:nth-child(3) {
                width: 145px;
                border-right: 1px solid #ddd;
              }
              &:nth-child(4) {
                width: 190px;
                border-right: 1px solid #ddd;
              }
            }
          }
          tbody {
            td {
              display: inline-block;
              padding: 0px 20px;
              text-align: center;
              margin: -2px;
              &:nth-child(1) {
                width: 115px;
                border-right: 1px solid #ddd;
                padding: 0px 20px 0px 0px;
              }
              &:nth-child(2) {
                width: 165px;
                border-right: 1px solid #ddd;
              }
              &:nth-child(3) {
                width: 145px;
                border-right: 1px solid #ddd;
              }
              &:nth-child(4) {
                width: 190px;
                border-right: 1px solid #ddd;
              }
            }
          }
        }
        .file-download {
          display: inline-flex;
          padding: 15px;
          border-right: 1px solid #ddd;
          margin-right: 15px;
          .icExcel {
            content: url("../../../assets/img/icExcel.png");
            width: 15px;
            height: 18px;
            margin-left: 3px;
          }
        }
        .url-register {
          display: inline-block;
          padding-top: 15px;
        }
        .btn_regis {
          background: #005197;
          color: #fff;
          font-size: 14px;
          line-height: 18px;
          padding: 6px 15px;
          cursor: pointer;
        }
      }
    }
  }
}
@media screen and (min-width: 1200px) {
  .training-service-page {
    .course-layout {
      .course-box {
        .course-content {
          .list-course-box {
            flex-basis: 66.6%;
            padding-right: 20px;
            .list-course {
              .course-item {
                overflow-y: auto;
                flex-basis: 48.5%;
                // height: 215px;
              }
            }
          }
        }
      }
    }
  }
}
@media screen and (max-width: 1199px) {
  .training-service-page {
    .course-layout {
      .course-box {
        .course-content {
          .list-course-box {
            flex-basis: 60%;
            padding-right: 20px;
            .list-course {
              height: 580px;
              overflow-y: auto;
              .course-item {
                //
                height: auto;
                &:nth-child(odd) {
                  margin-right: 0px;
                }
                .title {
                  height: 40px;
                }
              }
            }
          }
          .hot-course {
            flex-basis: 38%;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 1040px) and (min-width: 992px) {
  .training-service-page {
    .course-layout {
      .course-box {
        .course-content {
          flex-direction: row;
          .list-course-box {
            flex-basis: 66%;
            padding-right: 20px;
            .list-course {
              height: auto;
              .course-item {
                .course-title {
                  padding: 3px 0px;
                }
              }
            }
          }
        }
      }
    }
  }
}
@media screen and (max-width: 991px) {
  .training-service-page {
    .teachers-layout {
      padding: 30px 0;
    }
    .type-course-layout {
      padding: 30px 0;
      .type-course-list {
        .item-course {
          table {
            flex-basis: 100%;
            margin-bottom: 15px;
          }
          .blInfo {
            display: flex;
            flex-basis: 100%;
            flex-wrap: wrap;
          }
          .file-download {
            padding: 0px 15px;
          }
          .url-register {
            display: inline-block;
            padding-top: 0px;
          }
        }
      }
    }
    .course-layout {
      .course-box {
        padding: 30px 0 30px 0;
        .course-content {
          .hot-course {
            width: 100%;
            height: auto;
            margin-top: 20px;
            .course-item {
              .course-name {
                width: 100%;
              }
              .course-title {
                padding: 3px 0px;
              }
            }
            .hot-title {
              height: 40px;
              line-height: 40px;
            }
          }
          .list-course-box {
            .select-course-type {
              .type-course {
                height: 35px;
                line-height: 35px;
                &.active {
                  &::after {
                    border-left: 10px solid transparent;
                    border-right: 10px solid transparent;
                    border-top: 8px solid #125491;
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  .course-layout {
    .course-box {
      .course-content {
        flex-direction: column;
        .list-course-box {
          flex-basis: 100%;
          padding-right: 0px !important;
          .list-course {
            height: auto;
            .course-item {
              .course-title {
                padding: 3px 0px;
              }
            }
          }
        }
      }
    }
  }
}
@media screen and (max-width: 767px) {
  .training-service-page {
    .type-course-layout {
      padding: 20px 0 20px 0;
      .select-type-course {
        display: flex;
        flex-direction: column;
        .item-type-course {
          height: 40px;
          line-height: 40px;
        }
      }
      .title {
        font-size: 22px;
      }
      hr {
        margin: 15px auto;
      }
      .type-course-list {
        padding: 10px;
        .item-course {
          padding: 10px;
          .file-download {
            padding: 5px 0px !important;
          }
          .url-register {
            padding-top: 5px !important;
          }
          .course-date-time-cost {
            line-height: 20px;
          }
          .course-des {
            margin: 5px 0px;
            line-height: 20px;
          }

          table {
            thead {
              th {
                padding: 0px !important;
              }
            }
          }
        }
      }
    }
    .course-layout {
      .course-box {
        padding: 20px 0;
      }
    }
    .teachers-layout {
      .slick-slider {
      }
    }
  }
  .slick-list {
    .slick-track {
      .slick-slide {
        display: inline-flex;
      }
    }
  }
}
@media screen and (max-width: 583px) {
  .training-service-page {
    .type-course-layout {
      .type-course-list {
        .item-course {
          table {
            flex-basis: 100%;
            display: flex;
          }
          .blInfo {
            display: flex;
            flex-basis: 100%;
            // flex-wrap: nowrap;
            flex-direction: column;
          }
          .course-name {
            font-size: 18px;
            line-height: 22px;
            padding-left: 35px;
          }
          table {
            thead {
              th {
                display: inline-block;
                width: 135px !important;
                padding: 5px 0px !important;
                text-align: left;
                margin: 0px;
                border-right: none !important;
              }
            }
          }
          table {
            tbody {
              td {
                // display: inline-block;
                // width: 135px !important;
                padding: 5px 0px !important;
                text-align: left;
                margin: 0px;
                border-right: none !important;
              }
            }
          }
          .file-download {
            padding: 10px 0px;
            border-right: none;
            margin-right: 0px;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 374px) and (min-width: 320px) {
  .training-service-page {
    .course-layout {
      .course-box {
        .course-content {
          .list-course-box {
            .list-course {
              .course-item {
                .title {
                  height: auto;
                }
                .box-left {
                  float: left;
                  width: 100%;
                }
                .box-right {
                  float: left;
                  width: 100%;
                  text-align: left;
                }
              }
            }
          }
          .hot-course {
            .course-item {
              &.active {
                .box-left {
                  width: 100%;
                }
                .box-right {
                  width: 100%;
                  float: left;
                  text-align: left;
                }
              }
            }
          }
        }
      }
    }
  }
}
